.recommended {
  @include contentPaddingInline;

  &__products {
    display: grid;
    row-gap: 40px;

    @include onTablet {
      justify-items: center;
    }

    @include onDesktop {
      justify-items: stretch;
      grid-template-columns: repeat(3, 1fr);
      column-gap: 24px;
    }
  }
}
